___CSS_LOADER_EXPORT___.push([module.id, `* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

:disabled {
    background-color: dimgrey;
    color: linen;
    opacity: 1;
}

/* Общие стили */
body {
    font-family: Arial, sans-serif;
    background-color: #b2ebf2;
    color: #837e7e;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 20px;
    min-height: 100vh;
}

/* Контейнеры списков */
.container {
    background-color: #ebecf0;
    width: 300px;
    border-radius: 3px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: #333;
    border-radius: 6px;
}

/* Заголовки */
.wrapper__title {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-bottom: 10px;
}

.title-list {
    font-size: 20px;
    font-weight: bold;
    margin: 0;
    color: #898989;
}

.toggle__option {
    cursor: pointer;
    height: 21px;
}

/* Список задач */
.task__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

/* Элементы задач */
.task__item {
    background-color: #fff;
    padding: 25px 10px 10px;
    border-radius: 6px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    color: #333;
    cursor: grab;
    position: relative;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
}

.delete__task-button {
  display: none;
  top: 0;
  right: 0;
  padding: 5px;
  cursor: pointer;
  width: 25px;
  height: 25px;
  user-select: all;
  position: absolute;
}


.delete__task-button:hover {
    background-color: #f9f9fa;
}

.form {
    display: none;
}

/* Форма создания новой задачи */
.text__new-task {
    width: 100%;
    height: 4rem;
    resize: none;
    border: none;
    padding: 5px;
}

.text__new-task:focus {
    outline: none;
}

.container__button {
    display: flex;
    justify-content: space-between;
}

.button__add {
    width: 70%;
    border: none;
    background-color: #42a5f5;
    color: #fff;
    padding: 5px;
    margin-right: 3px;

}
.button__add:disabled {
    background-color: #cbcbcb;
}

.button__add:hover:enabled {
    background-color: #1e88e5;
    cursor: pointer;
}

.button__add:focus {
    outline: none;
}

.button__cancel {
    width: 30%;
    border: none;
    background-color: #e57373;
    color: #fff;
    padding: 5px;
    cursor: pointer;
}

.button__cancel:hover {
background-color: #f44336;
}

/* Стиль для карточек при перетаскивании */
.task__item:active {
    cursor: grabbing;
    opacity: 0.8;
}

/* Кнопка добавления */
.btn__add-card {
    color: #837e7e;
    padding: 8px 10px;
    border-radius: 3px;
    cursor: pointer;
    text-align: start;
    font-size: 14px;
}

.btn__add-card:hover {
    color: #0052cc;
}

/* Перетаскиваемый элемент */
.task__item.hidden {
    display: none;
  }

  .task__item.dragging {
    opacity: 0.5;
    cursor: grabbing;
  }

  /* Эффект при наведении на список */
  .task__list {
    min-height: 50px;
    border: 2px dashed transparent;
  }

  .hidden {
    display: none;
  }

  .placeholder {
    height: 50px;
    background-color: #f8f8f8;
    border: 1px dashed #ccc;
    margin: 5px 0;
    box-sizing: border-box;
    transition: border-color 0.3s ease;
  }
